import '../../assets/css/styles.css';
import 'remixicon/fonts/remixicon.css'
import { jsPDF } from "jspdf";
var QRCode = require("davidshimjs-qrcodejs");

const showMenu = (toggleId, navId) =>{
    const toggle = document.getElementById(toggleId),
    nav = document.getElementById(navId)
    
    if (toggle && nav) {
        toggle.addEventListener('click', ()=>{
            nav.classList.toggle('show-menu')
        })
    }
}
showMenu('nav-toggle','nav-menu')

const navLink = document.querySelectorAll('.nav__link')

function linkAction() {
    const navMenu = document.getElementById('nav-menu')
    if (navMenu) {
        navMenu.classList.remove('show-menu')
    }
}
if (navLink) {
    navLink.forEach(n => n.addEventListener('click', linkAction))
}

const sections = document.querySelectorAll('section[id]')

function scrollActive(){
    const scrollY = window.pageYOffset
    if (sections) {
        sections.forEach(current =>{
            const sectionHeight = current.offsetHeight
            const sectionTop = current.offsetTop - 50;
            let sectionId = current.getAttribute('id')
            if (sectionId) {
                if (scrollY > sectionTop && scrollY <= sectionTop + sectionHeight){
                    document.querySelector('.nav__menu a[href*=' + sectionId + ']').classList.add('active-link')
                } else {
                    document.querySelector('.nav__menu a[href*=' + sectionId + ']').classList.remove('active-link')
                }
            }
        })
    }
}
window.addEventListener('scroll', scrollActive)

function scrollTop(){
    const scrollTop = document.getElementById('scroll-top');
    if (scrollTop) {
        if(this.scrollY >= 200) scrollTop.classList.add('show-scroll'); else scrollTop.classList.remove('show-scroll')
    }
}
window.addEventListener('scroll', scrollTop)

const themeButton = document.getElementById('theme-button')
const darkTheme = 'dark-theme'
const iconTheme = 'ri-sun-line'

const selectedTheme = localStorage.getItem('selected-theme')
const selectedIcon = localStorage.getItem('selected-icon')

const getCurrentTheme = () => document.body.classList.contains(darkTheme) ? 'dark' : 'light'
const getCurrentIcon = () => themeButton.classList.contains(iconTheme) ? 'ri-moon-line' : 'ri-sun-line'

if (selectedTheme) {
  document.body.classList[selectedTheme === 'dark' ? 'add' : 'remove'](darkTheme)
  themeButton.classList[selectedIcon === 'ri-moon-line' ? 'add' : 'remove'](iconTheme)
}

if (themeButton) {
    themeButton.addEventListener('click', () => {
        document.body.classList.toggle(darkTheme)
        themeButton.classList.toggle(iconTheme)
        localStorage.setItem('selected-theme', getCurrentTheme())
        localStorage.setItem('selected-icon', getCurrentIcon())
    })
}


docReady(function() {
    var qrcode = new QRCode(document.getElementById('qr_code'), {
        text: "https://www.linkedin.com/in/zsolt-szelepcs%C3%A9nyi-811a511a2/",
        width: 128,
        height: 128,
        colorDark : "#000000",
        colorLight : "#fafafa",
        correctLevel : QRCode.CorrectLevel.H
    });
    var qrCodeElement = document.getElementById('qr_code')
    if (qrCodeElement) {
        qrCodeElement.addEventListener('click', function() {
            var pdf = new jsPDF({
                orientation: "landscape",
                unit: "mm",
                format: [94, 40]
            });
    
            pdf.setFontSize(15);
            pdf.text('Zsolt Szelepcsnyi', 43, 18);
            pdf.setFontSize(10);
            pdf.text('Frontend - Backend Developer', 43, 23);
            pdf.setFontSize(8);
            pdf.text('Hungary, Szeged', 43, 27);
    
            let base64Image = document.querySelector('#qr_code img').getAttribute('src')
            pdf.addImage(base64Image, 'png', 0, 0, 40, 40);
            pdf.save('zsolt_szelepcsenyi.pdf');
        })
        let qrcodeImage = document.querySelector('#qr_code img')
        qrcodeImage.setAttribute('width', '128')
        qrcodeImage.setAttribute('height', '128')
        qrcodeImage.setAttribute('alt', 'Zsolt Szelepcsenyi QRCode')
    }
});

function docReady(fn) {
    if (document.readyState === "complete" || document.readyState === "interactive") {
        setTimeout(fn, 1);
    } else {
        document.addEventListener("DOMContentLoaded", fn);
    }
}  

document.querySelectorAll('a[href^="#"]').forEach(anchor => {
    anchor.addEventListener('click', function (e) {
      e.preventDefault();
      if (this.getAttribute('href') !== '#') {
        document.querySelector(this.getAttribute('href')).scrollIntoView({
          behavior: 'smooth'
        });
      }
    });
});